$theme: #17c6ff;
$theme-secondary: #0f1b3a;
$theme-tertiary: #37c023;

$theme-blue: #004171;
$theme-darkerblue: #002e72;
$theme-darkgreen: #095d51;
$theme-darkergreen: #0a6c59;
$theme-green: #008b6e;
$theme-brown: #724a11;
$theme-yellow: #fffb00;
$theme-pink: #da5f6b;
$theme-brown-alt: #605442;

$theme-black: #161820;
$theme-text: #68645B;

$theme-gray: #F5F6F6;

$theme-shadow: 0 0 28px rgba(#3f609d, .14);

$font: 'Oswald',
sans-serif;
$font-text: 'Mulish',
sans-serif;
$font-alt: 'Nunito Sans',
sans-serif;

$font-awesome: 'Font Awesome 5 Free';

//mixin for responsive css
@mixin respond($breakpoint) {
  @if $breakpoint==phone {
    @media (max-width: 576px) {
      @content;
    }
  }

  @if $breakpoint==phone-min {
    @media (min-width: 577px) {
      @content;
    }
  }

  @if $breakpoint==phone-tabport {
    @media (min-width: 577px) and (max-width: 767px) {
      @content;
    }
  }

  @if $breakpoint==tabport {
    @media (max-width: 767px) {
      @content;
    }
  }

  @if $breakpoint==tabport-min {
    @media (min-width: 768px) {
      @content;
    }
  }

  @if $breakpoint==tabport-tabland {
    @media (min-width: 768px) and (max-width: 991px) {
      @content;
    }
  }

  @if $breakpoint==tabland {
    @media (max-width: 991px) {
      @content;
    }
  }

  @if $breakpoint==tabland-min {
    @media (min-width: 992px) {
      @content;
    }
  }

  @if $breakpoint==smdesktop {
    @media (max-width: 1200px) {
      @content;
    }
  }

  @if $breakpoint==tabland-smdesktop {
    @media (min-width: 992px) and (max-width: 1200px) {
      @content;
    }
  }

  @if $breakpoint==smdesktop-min {
    @media (min-width: 1201px) {
      @content;
    }
  }

  @if $breakpoint==bgdesktop {
    @media (max-width: 1401px) {
      @content;
    }
  }

  @if $breakpoint==bgdesktop-min {
    @media (min-width: 1401px) {
      @content;
    }
  }
}